.regular-btn {
  background-color: var(--primary);
  border-color: var(--primary);
  cursor: pointer;
  transition: all ease-in 0.2s;
  color: white;
  border-radius: 8px;
  font-weight: 700;
  height: 48px;
  font-size: 1.125rem;
}

.regular-btn:hover,
.regular-btn:active {
  background-color: var(--btnHover) !important;
  border-color: var(--btnHover) !important;
}

.dashed-btn {
  background-color: #fff;
  border: 1px solid var(--primary) !important;
  color: var(--primary) !important;
  transition: all ease-in 0.2s;
  font-size: 1.125rem;
  font-weight: 700;
  border-radius: 8px;
  height: 48px;
  cursor: pointer;
}

.dashed-btn:hover,
.dashed-btn:active {
  color: #fff !important;
  background-color: var(--btnHover) !important;
  border: 1px solid var(--btnHover) !important;
}

.regular-btn:focus,
.dashed-btn:focus {
  outline: none !important;
  box-shadow: none !important;
}

.heading-hr {
  width: 6rem !important;
  border: transparent !important;
  border-top: 3px solid #f05f40 !important;
  margin-top: 0;
  margin-bottom: 2rem;
}

